a:hover { cursor: pointer; font-weight: bold; }
a { text-decoration: none; color: #529afa;
    margin-inline-start: 0.5em; margin-inline-end: 0.5em; }
div { display: block; }
#root {
    text-align: center;
    letter-spacing: .2em;
    flex-direction: column;
    height: 100%;
    display: flex;
}
.top-space { padding: 10em; }
h1 { margin-block-start: 1em; margin-block-end: 0em; margin-inline-start: 1em; margin-inline-end: 1em; }
p { margin-block-start: .5em; margin-block-end: 1em; margin-inline-start: 0em; margin-inline-end: 0em; }


